import React from "react";
import PropTypes from "prop-types";
import { Seq } from "immutable";
import { FormLabel, FormSelect } from "../../components/style/export";
import PropertyStyle from "./shared-property-style";

export default function PropertyEnum({
  value,
  onUpdate,
  configs,
  sourceElement,
  internalState,
  state,
}) {
  let update = (val) => {
    if (configs.hook) {
      return configs
        .hook(val, sourceElement, internalState, state)
        .then((_val) => {
          return onUpdate(_val);
        });
    }

    return onUpdate(val);
  };
  let dataMaterial = [
    {
      name: "",
      img: "",
    },
    {
      name: " Granit",
      img: require("../../../public/images/Granit.png"),
    },
    {
      name: " cm thch",
      img: require("../../../public/images/marble.png"),
    },
    {
      name: "Gch lt",
      img: require("../../../public/images/tiles.png"),
    },
    {
      name: "Vn g",
      img: require("../../../public/images/wood.png"),
    },
    {
      name: " lt nn",
      img: require("../../../public/images/pavingStone.png"),
    },
    {
      name: "",
      img: require("../../../public/images/patternedTiles.png"),
    },
    {
      name: "Gch hoa vn",
      img: require("../../../public/images/stone.png"),
    },
  ];
  const WrapperMaterial = {
    display: "grid",
    gridTemplateColumns: "1fr 1fr",
    gap: "10px",
    width: "100%",
  };
  const TextName = {
    fontFamily: "Playpen Sans",
    fontSize: "10px",
    fontWeight: "400",
    lineHeight: "20px",
    textAlign: "center",
    background: "#00000040",
    borderBottomLeftRadius: "4px",
    borderBottomRightRadius: "4px",
    width: "100%",
    color: "#FFFFFF",
    padding: "2px 0",
  };
  return (
    <table
      className="PropertyEnum"
      style={{ ...PropertyStyle.tableStyle, padding: 0 }}
    >
      <tbody>
        <tr>
          {/* <td style={PropertyStyle.firstTdStyle}><FormLabel>{configs.label}</FormLabel></td> */}
          <td>
            <div style={WrapperMaterial}>
              {Seq(configs.values)
                .entrySeq()
                .map(([key, value], index) => (
                  <div
                    key={key}
                    style={{
                      borderRadius: "4px",
                      position: "relative",
                      background: `url(${dataMaterial[index].img})`,
                      backgroundSize: "100% 100%",
                      backgroundRepeat: "no-repeat",
                      width: "80px",
                      height: 85,
                      display: "flex",
                      alignItems: "flex-end",
                    }}
                    onClick={() => update(key)}
                  >
                    <div style={TextName}>{dataMaterial[index].name}</div>
                  </div>
                ))}
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  );
}

PropertyEnum.propTypes = {
  value: PropTypes.any.isRequired,
  onUpdate: PropTypes.func.isRequired,
  configs: PropTypes.object.isRequired,
  sourceElement: PropTypes.object,
  internalState: PropTypes.object,
  state: PropTypes.object.isRequired,
};
